.sticky {
	position: sticky;
	top: 63px;
	height: max-content;
}

.nav {
	width: 157px;
	grid-area: n;
	margin-left: -9px;
	border-radius: 2px;
}

.nav a {
	border-radius: 4px;
	display: flex;
	height: 30px;
	align-items: center;
	text-decoration: none;
	color: black;
	letter-spacing: 0.3px;
	font-size: 12.5px;
	padding-left: 9px;
	margin-left: -1px;
}

.icon {
	height: 20px;
	margin-right: 10px;
}

.nav_item:hover {
	background-color: #dce1e6;
}

.ad_container {
	overflow: hidden;
	padding: 10px 30px 10px 0px;
	box-sizing: border-box;
}

.ad_image {
	width: 100%;
	height: 100px;
	object-fit: cover;
	border-radius: 10px;
	background-image: url("../../images/Nav_Images/cyberpunk.jpg");
}
