.container {
    background:rgb(255, 255, 255);
    width:550px;
    height:190px;
    margin-top: 15px;
    border-radius: 4px;
    box-shadow: 0 1px 0 0 #acc9e15f,0 0 0 1px#E7E8EC;
}

.title{
    font-size: 13px;
    color:black;
    height:40px;
    padding:15px 20px;
    box-sizing: border-box;
}

.title .span{
    color:#939393;
    padding-left: 6px;
}

.photos{
    display: flex;
    justify-content: space-between;
    padding:7px 20px 20px 20px;
}
.photo{
    object-fit: cover;
    object-position: center;
    width:123px;
    height:123px;
}